import React, { Component } from 'react';
import {createElement} from './utils/script';
import tag from "./data/head";
import {getGeneratedPageURL} from './test';

let windowFrame;
class Frame extends Component {
    constructor(props){
        super(props);
        this.frame=React.createRef();
        this.state= {
            src:null
        }
    }

    componentDidMount(){
        /*
        windowFrame = this.frame.current.contentWindow;
        const head = windowFrame.document.getElementsByTagName("head")[0];

        tag.forEach((data)=>{
            head.append(createElement(data.element,data.attributes));
        })
        */
       const url = getGeneratedPageURL({
        html: '<p>Hello, world!</p>',
        css: 'body { background: blue; }',
        js: 'console.log("hi")'
      })
      this.setState({src:url});
    }

    onClick(){
        const script = createElement("script");
        script.type = "text/javascript";
        const code = "document.body.style.backgroundColor = red;";
        script.appendChild(document.createTextNode(code));
        windowFrame.document.body.appendChild(script);
    }

    render() {
        return (
            <div>
                 <iframe src={this.state.src} frameBorder="1" ref={this.frame}/>
                 <div onClick={this.onClick}>click me !</div>
            </div>
        );
    }
}

export default Frame;