import React, { Component } from 'react';
import {hsvToRgb} from './utils/colors';
function checkPos(pos){
    let result=pos;
    if(pos<0){
        result=0;
    } else if(pos>100){
        result=100;
    }
    return result;
}




class Selector extends Component {
    constructor(props){
        super(props);
        this.state={
            left:100,
            top:50,
            h:0,
            s:1,
            isDragging:false,
            dragX:0,
            dragY:0
        }
        this.ref=React.createRef();
        this.onTouchStart=this.onTouchStart.bind(this);
        this.onDragStart=this.onDragStart.bind(this);
        this.onDrag=this.onDrag.bind(this);
        this.onDragEnd=this.onDragEnd.bind(this);
        this.onTouchEnd=this.onTouchEnd.bind(this);
        this.onTouchMove=this.onTouchMove.bind(this);
    }

    onDragStart(e){
        const canvas = this.ref.current;
        const ctx = canvas.getContext("2d");

        canvas.width = 48;
        canvas.height = 48;

        let region = new Path2D();
        region.arc(canvas.width/2,canvas.height/2,24,0,Math.PI*2);
        region.arc(canvas.width/2,canvas.height/2,12,0,Math.PI*2);

        ctx.clip(region,"evenodd");

        ctx.fillStyle="#FFFFFF";
        ctx.fillRect(0, 0, canvas.width, canvas.height);

        const element = document.getElementsByClassName("App");
        const rond = document.getElementsByClassName("rond");
        rond[0].style.cursor="none";
        element[0].style.cursor="none";
        e.dataTransfer.setDragImage(canvas, 0, 0);
        e.dataTransfer.setData("text/plain", "Text to drag");
        document.ondragover = (event) => {
            event = event || window.event;
            const dragX = event.pageX;
            const dragY = event.pageY;
            this.setState({dragX,dragY});
        }
    }

    onTouchStart(e){
        this.setState({isDragging:true});
    }

    onTouchMove(e){
        let X= e.touches[0].pageX-this.props.offsetLeft;
        let Y = e.touches[0].pageY-this.props.offsetTop;

        let left = checkPos(X/300*100);
        let top = checkPos(Y/300*100);

        let Xt = (left-50)/50;
        let Yt = (top-50)/50;

        let deg;

        const r = Math.sqrt(Math.pow(left-50,2)+Math.pow(top-50,2));

        if(Yt>=0){
            deg=Math.atan2(Yt,Xt);
        }else {
            deg=2*Math.PI+Math.atan2(Yt,Xt);
        }

        if(r>=50){
            top=50+50*Math.sin(deg);
            left=50+50*Math.cos(deg);
        }

        let color = hsvToRgb({h:360-deg*180/Math.PI,s:r/50,v:this.props.value});

        this.setState({
            top:top,
            left:left,
            h:360-deg*180/Math.PI,
            s:r/50
        });
    }

    onDrag(e){
        if(this.state.dragX !== 0 && this.state.dragY !== 0){
            let X= this.state.dragX-this.props.offsetLeft;
            let Y = this.state.dragY-this.props.offsetTop;

            let left = checkPos(X/300*100);
            let top = checkPos(Y/300*100);

            let Xt = (left-50)/50;
            let Yt = (top-50)/50;

            let deg;

            const r =Math.sqrt(Math.pow(left-50,2)+Math.pow(top-50,2));

            if(Yt>=0){
                deg=Math.atan2(Yt,Xt);
            }else {
                deg=2*Math.PI+Math.atan2(Yt,Xt);
            }

            if(r>=50){
                top=50+50*Math.sin(deg);
                left=50+50*Math.cos(deg);
            }

            let color = hsvToRgb({h:360-deg*180/Math.PI,s:r/50,v:this.props.value});

            this.setState({
                top:top,
                left:left,
                h:360-deg*180/Math.PI,
                s:r/50
            });
        }
    }

    onDragEnd(e){
        const element = document.getElementsByClassName("App");
        const rond = document.getElementsByClassName("rond");
        rond[0].style.cursor="default";
        element[0].style.cursor="default";
    }

    onTouchEnd(e){
        this.setState({isDragging:false});
    }

    render() {
        const color = hsvToRgb({h:this.state.h,s:this.state.s,v:this.props.value});
        return (
            <div
            draggable

            style={{
                top:this.state.top-5+"%",
                left:this.state.left-5+"%",
                backgroundColor:`rgb(${color.r*255},${color.g*255},${color.b*255})`
            }}

            onDragStart={(e)=>
                this.onDragStart(e)
            }
            onDrag={(e)=>
                this.onDrag(e)
            }

            onDragEnd={(e)=>
                this.onDragEnd(e)
            }

            onTouchStart={(e)=>
                this.onTouchStart(e)
            }

            onTouchMove={(e)=>
                this.onTouchMove(e)
            }

            onTouchEnd={
                this.onTouchEnd
            }

            className="rond">
                <canvas
                    className="hide"

                    ref={
                        this.ref
                    }
                />
            </div>
        );
    }
}

export default Selector;