import React, { Component } from 'react';
import {drawChroma,initCanvas} from './utils/utils';
import ValueHSV from './ValueHSV';
import Selector from './Selector';

class Color extends Component {
    constructor(props){
        super(props);
        this.ref=React.createRef();
        this.mask=React.createRef();
        this.refSel=React.createRef();
        this.state={
            offsetLeft:null,
            offsetTop:null,
            style:{
                left:`calc(100% - 15px)`,
                backgroundColor:"rgb(255,255,255)",
            },
            dragX:1,
            value:1
        }

        this.onDrag=this.onDrag.bind(this);
    }
    componentDidMount(){
        const canvas = this.mask.current;
        const offsetLeft=this.refSel.current.offsetLeft;
        const offsetTop=this.refSel.current.offsetTop;

        window.addEventListener('resize',this.handleResize);

        initCanvas(canvas);
        drawChroma(1);

        this.setState({offsetTop,offsetLeft});

    }

    onDrag=(e,boundingRect,dragX)=>{
        if(dragX !== 0 && boundingRect){
            const offsetLeft=boundingRect.x;
            const width = boundingRect.width;
            let X = dragX-offsetLeft;
            let position;
            if(X>width){
                position=1
            } else if(X<0){
                position=0;
            } else {
                position = X/width;
            }

            drawChroma(position);

            this.setState({
                value:position,
                style:{
                    left:`calc(${position*100}% - 15px)`,
                    backgroundColor:`rgb(${position*255},${position*255},${position*255})`
                }
            });
        }
    }

    onTouchMove=(e,boundingRect)=>{
        let dragX= e.touches[0].pageX;
        if(dragX !== 0 && boundingRect){
            const offsetLeft=boundingRect.x;
            const width = boundingRect.width;
            let X = dragX-offsetLeft;
            let position;
            if(X>width){
                position=1
            } else if(X<0){
                position=0;
            } else {
                position = X/width;
            }

            drawChroma(position);

            this.setState({
                value:position,
                style:{
                    left:`calc(${position*100}% - 15px)`,
                    backgroundColor:`rgb(${position*255},${position*255},${position*255})`
                }
            });
        }
    }

    componentWillMount(){
        window.removeEventListener('resize',this.handleResize);
    }

    handleResize=()=>{
        const offsetLeft=this.refSel.current.offsetLeft;
        const offsetTop=this.refSel.current.offsetTop;

        this.setState({offsetTop,offsetLeft});
    }
    render() {
        return (
            <div

            ref={this.refSel}

            className="wrapper"
            >
                 <div className="canvas_wrapper">
                    <canvas className="mask" ref={this.mask} />
                </div>
                <ValueHSV onDrag={this.onDrag} onTouchMove={this.onTouchMove} style={this.state.style}/>
                <Selector value={this.state.value} offsetTop={this.state.offsetTop} offsetLeft={this.state.offsetLeft}/>
            </div>
        );
    }
}

export default Color;