const getRandomColor = function() {
    var letters = '0123456789ABCDEF';
    var color = '#';
    for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }

const hsvToRgb = function (hsv){
    const h=hsv.h,s=hsv.s,v=hsv.v;
    const c = v*s;
    const hp = h/60;
    const x = c * (1-Math.abs(hp%2-1));
    const m = v-c;

    let rgb = {r:0,g:0,b:0};

    if(hp >= 0 && hp <= 1)
    {
        rgb.r = c;
        rgb.g = x;
    } else if(hp>1 && hp <= 2){
        rgb.r=x;
        rgb.g=c;
    } else if(hp>2 && hp <= 3){
        rgb.g = c;
        rgb.b = x;
    } else if(hp>3 && hp <= 4){
        rgb.g = x;
        rgb.b = c;
    } else if(hp>4 && hp <= 5){
        rgb.r=x;
        rgb.b=c;
    } else if(hp>5 && hp <= 6){
        rgb.r=c;
        rgb.b=x;
    }

    rgb.r+=m;
    rgb.g+=m;
    rgb.b+=m;

    return rgb;
}

export {hsvToRgb};