const extractEntries = (dataStructure)=>{
    let result;
    if(dataStructure instanceof Map){
        result = dataStructure.entries();
    } else if (dataStructure instanceof Array){
        
    } else if(dataStructure instanceof Object){
        result = Object.entries(dataStructure);
    }
    
    return result;
}


const createElement = (element,attributes)=>{
    const meta = document.createElement(element);
    if(attributes){
        const entries = extractEntries(attributes);
        for(let [key,value] of entries){
            meta.setAttribute(key,value);
        }
    }
    return meta;
}

export {createElement};