const createShader = (gl, type, source) => {
    var shader = gl.createShader(type);
    gl.shaderSource(shader, source);
    gl.compileShader(shader);
    var success = gl.getShaderParameter(shader, gl.COMPILE_STATUS);
    if (success) {
      return shader;
    }
   
    gl.deleteShader(shader);
  }

const createProgram=(gl, vertexShader, fragmentShader) => {
    var program = gl.createProgram();
    gl.attachShader(program, vertexShader);
    gl.attachShader(program, fragmentShader);
    gl.linkProgram(program);
    var success = gl.getProgramParameter(program, gl.LINK_STATUS);
    if (success) {
      return program;
    }
   
    gl.deleteProgram(program);
  }

const resize = (gl) => {
    var realToCSSPixels = window.devicePixelRatio;
  
    // Lookup the size the browser is displaying the canvas in CSS pixels
    // and compute a size needed to make our drawingbuffer match it in
    // device pixels.
    var displayWidth  = Math.floor(gl.canvas.clientWidth  * realToCSSPixels);
    var displayHeight = Math.floor(gl.canvas.clientHeight * realToCSSPixels);
  
    // Check if the canvas is not the same size.
    if (gl.canvas.width  !== displayWidth ||
        gl.canvas.height !== displayHeight) {
  
      // Make the canvas the same size
      gl.canvas.width  = displayWidth;
      gl.canvas.height = displayHeight;
    }
  }





let gl,program,positionAttributeLocation,resolutionUniformLocation,colorsUniformLocation,valueUniformLocation,positionBuffer,vertexIdLoc,numVertsLoc,resolutionLoc;
let numVerts,idBuffer;




const initCanvas = (canvas) =>{
gl = canvas.getContext("webgl");
if(!gl){
    console.log("Webgl n'est pas supportÃ©");
}
canvas.width = 600;
canvas.height = 600;

const vertexShaderSource=`
attribute float vertexId;
uniform float numVerts;
uniform vec2 resolution;

#define PI radians(180.0)

void main() {
  float numSlices = 1024.0;
  float sliceId = floor(vertexId / 3.0);
  float triVertexId = mod(vertexId, 3.0);
  float edge = triVertexId + sliceId;
  float angleU = edge / numSlices;  // 0.0 to 1.0
  float angle = angleU * PI * 2.0;
  float radius = step(triVertexId, 1.5);
  vec2 pos = vec2(cos(angle), sin(angle)) * radius;

  float aspect = resolution.y / resolution.x;
  vec2 scale = vec2(aspect, 1);
  
  gl_Position = vec4(pos * scale, 0, 1);
}
`;

const fragmentShaderSource = `
// fragment shaders don't have a default precision so we need
// to pick one. mediump is a good default
precision mediump float;
uniform vec4 u_colors;
uniform float u_value;
varying vec2 v_position;
float center = 300.0;
float r = 300.0;
float pi = 3.141592;
float conv = pi/180.0;

vec3 hsv2rgb( in vec3 hsl )
{
    float c = hsl.y*hsl.z;
    float hp = hsl.x/60.0;
    float x = c*abs(1.0-abs(mod(hp,2.0)-1.0));
    vec3 rgb;
    vec3 rgbp;
    if(hp>=0.0 && hp <= 1.0)
    {
        rgbp = vec3(c,x,0.0);
    } else if(hp>1.0 && hp <= 2.0){
        rgbp = vec3(x,c,0.0);
    } else if(hp>2.0 && hp <= 3.0){
        rgbp = vec3(0.0,c,x);
    } else if(hp>3.0 && hp <= 4.0){
        rgbp = vec3(0.0,x,c);
    } else if(hp>4.0 && hp <= 5.0){
        rgbp = vec3(x,0.0,c);
    } else if(hp>5.0 && hp <= 6.0){
        rgbp = vec3(c,0.0,x);
    }

    float m = hsl.z - c;
    rgb = vec3(rgbp.x+m,rgbp.y+m,rgbp.z+m);

    return rgb;
}

vec3 hsl2rgb( in vec3 hsl )
{
    float c = (1.0-abs(2.0*hsl.z-1.0))*hsl.y;
    float hp = hsl.x/60.0;
    float x = c*abs(1.0-abs(mod(hp,2.0)-1.0));
    vec3 rgb;
    vec3 rgbp;
    if(hp>=0.0 && hp <= 1.0)
    {
        rgbp = vec3(c,x,0.0);
    } else if(hp>=1.0 && hp <= 2.0){
        rgbp = vec3(x,c,0.0);
    } else if(hp>=2.0 && hp <= 3.0){
        rgbp = vec3(0.0,c,x);
    } else if(hp>=3.0 && hp <= 4.0){
        rgbp = vec3(0.0,x,c);
    } else if(hp>=4.0 && hp <= 5.0){
        rgbp = vec3(x,0.0,c);
    } else if(hp>=5.0 && hp <= 6.0){
        rgbp = vec3(c,0.0,x);
    }

    float m = hsl.z - c/2.0;
    rgb = vec3(rgbp.x+m,rgbp.y+m,rgbp.z+m);

    return rgb;
}

float atan2 ( in vec2 coord ) {
    float rad = 0.0;
    float x = coord.x;
    float y = coord.y;

    if(y>0.0){
        rad = atan(y,x);
    } else {
        rad = pi*2.0+atan(y,x);
    }

    if (x == 0.0){
        if(y > 0.0){
            rad = 0.0;
        } else {
            rad=(-pi/2.0);
        }
    }
    
    return rad;
}

void main() {
    // gl_FragCoord respresent the coord of the pixel
    float x = (gl_FragCoord.x-center)/r;
    float y = (gl_FragCoord.y-center)/r;
    float deg =degrees(atan2(vec2(x,y)));
    float rs = sqrt(pow(x,2.0)+pow(y,2.0));
    vec4 color;
    if(rs>r){
        color=vec4(0.0,0.0,0.0,0.0);
    }else{
        color = vec4(hsv2rgb(vec3(deg,rs,u_value)),1);
    }
    gl_FragColor = color;
}`

let vertexShader = createShader(gl, gl.VERTEX_SHADER, vertexShaderSource);
let fragmentShader = createShader(gl, gl.FRAGMENT_SHADER, fragmentShaderSource);

program = createProgram(gl, vertexShader, fragmentShader);

vertexIdLoc = gl.getAttribLocation(program, 'vertexId');
numVertsLoc = gl.getUniformLocation(program, 'numVerts');
resolutionLoc = gl.getUniformLocation(program, 'resolution');

colorsUniformLocation = gl.getUniformLocation(program, "u_colors");
valueUniformLocation = gl.getUniformLocation(program, "u_value");

numVerts = 1024 * 3;
const vertexIds = new Float32Array(numVerts);
vertexIds.forEach((v, i) => {
  vertexIds[i] = i;
});

idBuffer = gl.createBuffer();
gl.bindBuffer(gl.ARRAY_BUFFER, idBuffer);
gl.bufferData(gl.ARRAY_BUFFER, vertexIds, gl.STATIC_DRAW);

//resize(gl);


}



const drawChroma=(value)=>{
    gl.viewport(0, 0, gl.canvas.width, gl.canvas.height);

    // Clear the canvas
    gl.clearColor(0, 0, 0, 0);
    gl.clear(gl.COLOR_BUFFER_BIT);

    // Tell it to use our program (pair of shaders)
    gl.useProgram(program);

    {
        // Turn on the attribute
        gl.enableVertexAttribArray(vertexIdLoc);
    
        // Bind the id buffer.
        gl.bindBuffer(gl.ARRAY_BUFFER, idBuffer);
    
        // Tell the attribute how to get data out of idBuffer (ARRAY_BUFFER)
        const size = 1;          // 1 components per iteration
        const type = gl.FLOAT;   // the data is 32bit floats
        const normalize = false; // don't normalize the data
        const stride = 0;        // 0 = move forward size * sizeof(type) each iteration to get the next position
        const offset = 0;        // start at the beginning of the buffer
        gl.vertexAttribPointer(
            vertexIdLoc, size, type, normalize, stride, offset);
      }
    
      // tell the shader the number of verts
      gl.uniform1f(numVertsLoc, numVerts);
      // tell the shader the resolution
      gl.uniform2f(resolutionLoc, gl.canvas.width, gl.canvas.height);
        gl.uniform4f(colorsUniformLocation, 0, 0, 1, 1);
        gl.uniform1f(valueUniformLocation, value);
    
      const offset = 0;
      gl.drawArrays(gl.TRIANGLES, offset, numVerts);
 
}

  export {drawChroma,initCanvas};